.container {
    width: 250px;
    height: 300px;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.14);
    position: relative;
    padding: 1.8rem;
    border: 2px solid #c3c6ce;
    transition: 0.5s ease-out;
    overflow: visible;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
    
}

.card-details {
    color: black;
    height: 100%;
    grid-gap: .5em;
    gap: .5em;
    display: grid;
    place-content: center;
    justify-content: center;
}

.card-button {
    -webkit-transform: translate(-50%, 125%);
            transform: translate(-50%, 125%);
    width: 60%;
    border-radius: 1rem;
    border: none;
    background-color: #008bf8;
    color: #fff;
    font-size: 1rem;
    padding: .5rem 1rem;
    position: absolute;
    left: 50%;
    bottom: 0;
    opacity: 0;
    transition: 0.3s ease-out;
}

.text-body {
    color: rgb(134, 134, 134);
}

.text-title {
    font-size: 1.5em;
    font-weight: bold;
    color: white;
    align-self: center;
    padding-bottom: 8%;
    margin: none;
    text-align: center;
}

.container:hover {
    border-color: #008bf8;
    box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.25);
}

.container:hover .card-button {
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    opacity: 1;
}

